Date.prototype.Format = function (fmt) {
    var o = {
        "M+": this.getMonth() + 1, //月份
        "d+": this.getDate(), //日
        "h+": this.getHours(), //小时
        "m+": this.getMinutes(), //分
        "s+": this.getSeconds(), //秒
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度
        "S": this.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
};
var _self , logRes , did
export default {
	data() {
		return {
			OrderList: [],
			TheLists:[],
			TabCur: 0,
			scrollLeft: 0
		};
	},
	onLoad(option){
		_self = this;
		logRes = _self.checkLogin('../order/order',1);
		console.log(logRes[0]);
		_self.did = logRes[0];
		
		uni.request({
			url: _self.apiServer+'mall/order/allOrders',
			method: 'GET',
			data: {
				id : _self.did
			},
			success: res => {
				_self.OrderList = res.data;
				_self.TheLists=_self.OrderList;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			var num =this.TabCur-1;
			_self.TheLists=[];
			if(num>=0){
				_self.OrderList.forEach(function(e){
					if(e.delivery==num){
						_self.TheLists.push(e);						
					}
				});
				}else{
					_self.TheLists=_self.OrderList;
				}
				console.log(_self.TheLists)
		},
		details(item){
			//console.log(item);
			var order = JSON.stringify(item);
			
			uni.redirectTo({
				url: 'order-detail/order-detail?order='+order
			});
		},
		formatDate(timestramp) {
			     return new Date(timestramp).Format('yyyy-MM-dd');
			 }
	}
}

